input{
	caret-color: black;
}
body {
	margin: 0px;
	font-family: $mainFont;
	color: #003772;
	background-color: #F7FAFF;
}

button:hover,
button:focus {
	cursor: pointer;
}
button:active{
	background: #B94C40;
}
* {
	box-sizing: border-box;
}

ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

a {
  color: inherit;
  text-decoration: inherit; /* no underline */
}
// a, a:hover, a:focus, a:active { 
//       text-decoration: none; 
//       color: inherit; 
//  }

// img {
//     display: block;
// }

h1,
h2,
h3,
h4,
h5,
h6,
p {
	margin-top: 0;
	margin-bottom: 0;
}
h1 { //default style for main page title
	font-style: normal;
	font-weight: 600;
	font-size: rem(24);
	line-height: rem(28);
	text-transform: uppercase;
}
.ibg{
	background-position: center;
	background-repeat: no-repeat;
	&--contain{
		background-size: contain;
	}
	&--cover{
		background-size: cover;
	}
}
div.hidden{
	display: none;
}